import React, { useState } from 'react'
import Spinner from '../Spinner'
import InnerRing from './InnerRing'
import RingTwo from './RingTwo'
import './styles.css'

const noop = () => {}

const Projects = ({
  xy,
  setScale = noop,
  scale = 1
}) => {
  const hovering = [ useState(false), useState(false) ]
  const BASE_CIRCLE_SIZE = 50

  return (
    <div
      className="projects"
      onWheel={({ deltaY }) => {
        if (deltaY > 0)
          setScale(scale + 0.02)
        else
          setScale(scale - 0.02)
      }}
      style={{
        transform: `translate(${(xy.x)}px, ${xy.y}px) scale(${scale})`
      }}
    >
      <RingTwo
        circleSize={BASE_CIRCLE_SIZE * 1}
        hovering={hovering[0][0]}
        onMouseEnter={() => {
          hovering[0][1](true)
          hovering[1][1](false)
        }}
        onMouseLeave={() => hovering[0][1](false)}
      >
        <InnerRing
          circleSize={BASE_CIRCLE_SIZE}
          hovering={hovering[1][0]}
          onMouseEnter={() => {
            hovering[1][1](true)
            hovering[0][1](false)
          }}
          onMouseLeave={() => {
            hovering[1][1](false)
            hovering[0][1](true)
          }}
        >
          <Spinner
            upperText="PROJECTS"
            lowerText="PROJECTS"
          />
        </InnerRing>
      </RingTwo>
    </div>
  )
}

export default Projects
